import React, { useState, useEffect, Fragment, } from "react";
import { useDispatch, useSelector } from "react-redux";
import MainLayout from '../layouts/MainLayout';
import Group from '../components/Group';
import Events from '../components/Events';
import Memberslider from '../components/Memberslider';
import Postview from '../components/Postview';
import Load from '../components/Load';
import Pagetitle from '../components/Pagetitle';
import fileDownload from 'js-file-download';
import api from '../utils/api';
import axios from 'axios';
const Home = () => {
    const dispatch = useDispatch();
    const [myCryptoTweets, setMycryptoTweets]=useState([]);
    const [searchword, setSearchword]=useState('');
    const {
        user, vcoin, isauthenticated
    } = useSelector(state => state.auth); // here, indicate reducer
    
    useEffect(() => {
        const setblogs = async()=>{
            const res= await api.get('/blog');
            setMycryptoTweets(res.data);
        }
        if(isauthenticated&&user) setblogs();
    },[isauthenticated]);

    const sendDataToParent = (index) => { // the callback. Use a search word
        setSearchword(index);
    };
    const keyPress = async (keyCode,e) => { // the callback. Use a keycode    
        if(keyCode===13&&searchword!==""){
            e.preventDefault();            
            // var res=await RegisterContract.userAddressFromUsername(searchword);

        }
        if(keyCode===13&&searchword===""){
            e.preventDefault();            
        }
    };
    const buyVcoin=async(id,price)=>{ 
        if(vcoin<price*50)
            window.alert("Wallet balance is not enough to buy");
        else
            await Download(id);
    }
    const Download=async(id)=>{
        myCryptoTweets.map((cryptotweet,i)=>{
            let filename;
            if(id==cryptotweet._id){
                if(cryptotweet.filetype==="video")
                    filename=cryptotweet.tokenName+'.mp4';
                else filename=cryptotweet.tokenName+'.jpg';
                axios.get(cryptotweet.originmetaurl, {
                    responseType: 'blob',
                })
                .then(async(res) => {
                   await fileDownload(res.data, filename);
                    
                })
            }
        }) 
    }
    const addlike = async(id)=>{
        try {
            await api.put(`/blog/like/${id}`);
            const res= await api.get('/blog');
            setMycryptoTweets(res.data);
            
        } catch (error) {
            console.log("error",error.response.data);
        }
    }
    const addcomment = async(id)=>{
        try {
            await api.put(`/blog/comment/${id}`);
            const res= await api.get('/blog');
            setMycryptoTweets(res.data);
            
        } catch (error) {
            console.log("error",error.response.data);
        }
    }
    return (
        <Fragment>           
            <MainLayout>
                <div className="middle-sidebar-bottom">
                    <div className="middle-sidebar-left">
                        <div className="row feed-body">
                            <div className="col-xl-8 col-xxl-9 col-lg-8">
                                <Pagetitle title="Home" keyPress={keyPress} sendDataToParent={sendDataToParent} searchword={searchword}/>
                                {isauthenticated&&user?
                                myCryptoTweets.map((array,i)=>{
                                    return(
                                        <div key={i} className="w-100" >
                                            {/* <Videoslider id={array.tokenId} time="times"  user={array.currentOwner} avater="user.png" videourl={array.tokenURI} videoimage="user.png"  des={array.tokenName}/> */}
                                            <Postview addcomment={addcomment} addlike={addlike} buyVcoin={buyVcoin} Download={Download} avater="user.png" creator={array.creator} date={user.date} id={array._id} time={array.date} price={array.price} des={array.description}  videoOption={array.filetype} sourceURI={array.markedmetaurl} likes={array.likes.length} comments={array.reposts.length}/>                                
                                        </div>
                                        );
                                    }
                                ):""}
                                <Load />
                            </div>
                            <div className="col-xl-4 col-xxl-3 col-lg-4 ps-lg-0">
                                <div className="card w-100 shadow-xss rounded-xxl border-0 mb-3">
                                    <div className="card-body d-flex align-items-center  p-4">
                                        <h4 className="fw-700 mb-0 font-xssss text-grey-900">Trending</h4>
                                    </div>
                                    <Memberslider />
                                </div>
                                <Group />
                                <Events />
                            </div>
                        </div>
                    </div>
                </div>
            </MainLayout>
        </Fragment>
    );
}

export default Home;