import React, { useState, Fragment } from "react";
import { useHistory, Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import Header from "../components/Header";
import Leftnav from "../components/Leftnav";
import Rightchat from "../components/Rightchat";
import Appfooter from "../components/Appfooter";
import Popupchat from "../components/Popupchat";
import { rgba } from "jimp";
import api from '../utils/api';
import axios from "axios";
import { set_profile} from "../redux/actions/auth";
const Account = () => {
    let history = useHistory();
    let dispatch=useDispatch();
    const [profileData, setprofileData] = useState({
        name: '',
        city: '',
        country: ''
    });
    const [imgData, setImgData] = useState({
        avatar:null,
        backimage:null
    });
    const [desc, setdesc] = useState("");
    const { name, city, country } = profileData;
    const { avatar, backimage } = imgData;

    const onChange = async(e) =>{
        setprofileData({ ...profileData, [e.target.name]: e.target.value });
    }    
    const capturefile = async(e) =>{
        if(e.target.files[0].type.indexOf("image") !== -1)
        setImgData({ ...imgData, [e.target.name]: e.target.files[0] });
    }
    const keydown = (e) => {
        return e.keyCode;
    };
    const eventdesc = (e) => {
        let length = e.target.value.length;
        if (length <= 299) setdesc(e.target.value);
        if (keydown === 8 || keydown === 46) setdesc(e.target.value);
    };
    const saveprofile = (e)=>{
        e.preventDefault();
        console.log(avatar);
        let formData = new FormData();
        formData.append("name", name);
        formData.append("city", city);
        formData.append("country", country);
        formData.append("bio", desc);
        formData.append("avatar", avatar);
        formData.append("backimage", backimage);
        axios
        .post("https://viavix.com/api/profile", formData, {
          headers: {
            "x-auth-token": localStorage.getItem("token"),
            "Content-Type": "multipart/form-data",
          },
          timeout:20000
        })
        .then(function (res) {            
          dispatch(set_profile(res.data));
            history.push('/profile');
        });
    }    
    return (
    <Fragment>
      <Header />
      <Leftnav />
      {/* <Rightchat /> */}

      <div className="main-content bg-lightblue theme-dark-bg right-chat-active">
        <div className="middle-sidebar-bottom">
          <div className="middle-sidebar-left">
            <div className="middle-wrap">
              <div className="card w-100 border-0 bg-white shadow-xs p-0 mb-4">
                <div className="card-body p-4 w-100 bg-black border-0 d-flex rounded-3">
                  <h4 className="font-xs text-white fw-600 ms-4 mb-0 mt-2">
                    Update profile
                  </h4>
                </div>
                <div className="card-body p-4 w-100 border-0 ">
                  <div className="row justify-content-center">
                    <div className="col-lg-4 text-center">
                      <input
                        type="file"
                        name="avatar"
                        id="avatarfile"
                        className="input-file"
                        onChange={capturefile}
                      />
                      <label htmlFor="avatarfile">
                        <figure className="avatar ms-auto me-auto mb-0 mt-2 mb-4 w100">
                          <img
                            src={avatar?  URL.createObjectURL(avatar):`https://via.placeholder.com/460x460?text=Avatar`}
                            alt="avater"
                            width="100"
                            height="100"
                            className="shadow-sm rounded-circle w-100"
                          />
                        </figure>
                      </label>
                    </div>
                  </div>

                  <form action="#">
                    <div className="row">
                      <div className="col-lg-12 mb-3">
                        <div className="form-group">
                          <label className="mont-font fw-600 font-xsss mb-2">
                            Name
                          </label>
                          <input type="text" name="name" onChange={onChange} className="form-control font-sm" />
                        </div>
                      </div>
                    </div>

                    <div className="row">
                      <div className="col-lg-12 mb-3">
                        <label className="mont-font fw-600 font-xsss mb-2 text-dark">
                          Bio
                        </label>
                        <textarea
                            value={desc}
                            onChange={eventdesc}
                            onKeyDown={keydown}
                            className="form-control font-sm mb-0 p-3 h100 bg-greylight lh-16"
                            rows="5"
                            placeholder="Write 300 letters about you "
                        ></textarea>
                      </div>
                    </div>

                    <div className="row">
                      <div className="col-lg-6 mb-3">
                        <div className="form-group">
                          <label className="mont-font fw-600 font-xsss mb-2">
                            City
                          </label>
                          <input type="text" name="city" onChange={onChange} className="form-control font-sm" />
                        </div>
                      </div>
                      <div className="col-lg-6 mb-3">
                        <div className="form-group">
                          <label className="mont-font fw-600 font-xsss mb-2">
                            Country
                          </label>
                          <input type="text" name="country" onChange={onChange} className="form-control font-sm" />
                        </div>
                      </div>
                    </div>

                    <div className="row">
                      <div className="col-lg-12 mb-3">
                        <div className="card mt-3 border-0">
                          <div className="card-body d-flex justify-content-between align-items-end p-0">
                            <div className="form-group mb-0 w-100">
                              <input
                                type="file"
                                name="backimage"
                                id="file"
                                className="input-file"
                                onChange={capturefile}
                              />
                              <label
                                htmlFor="file"
                                className="rounded-3 text-center bg-white btn-tertiary js-labelFile p-4 w-100 border-dashed"
                                style={{backgroundImage: `${backimage?`url("${URL.createObjectURL(backimage)}")`:`url("https://via.placeholder.com/460x460?text=+")`}`, backgroundSize:"100% 100%"}}
                              >
                                <span className="js-fileName">
                                  Pick background
                                </span>
                              </label>
                            </div>
                          </div>
                        </div>
                      </div>

                      <div className="col-lg-12 d-flex justify-content-between">
                        <button
                          onClick={saveprofile}
                          className="bg-black text-center text-white font-xsss fw-600 p-3 w175 badge-pill d-inline-block"
                        >
                          Save
                        </button>
                        <Link
                          to='/profile'
                          className="bg-black text-center text-white font-xsss fw-600 p-3 w175 badge-pill d-inline-block"
                        >
                          Back
                        </Link>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Popupchat />
      <Appfooter />
    </Fragment>
  );
};

export default Account;
